import React, { useState } from 'react';

/**
 * TransactionList component - displays a list of trading days
 */
const TransactionList = ({ days, startEditingDay, archiveDay, deleteDay, setActiveSection }) => {
  const [sortField, setSortField] = useState('day');
  const [sortDirection, setSortDirection] = useState('desc');
  
  // Handle sort
  const handleSort = (field) => {
    if (sortField === field) {
      // Toggle direction if clicking the same field
      setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
    } else {
      // Set new field and default to descending
      setSortField(field);
      setSortDirection('desc');
    }
  };
  
  // Sort days
  const sortedDays = [...days].sort((a, b) => {
    let comparison = 0;
    
    switch (sortField) {
      case 'day':
        comparison = a.day - b.day;
        break;
      case 'date':
        comparison = new Date(a.date) - new Date(b.date);
        break;
      case 'percentage':
        comparison = a.percentage - b.percentage;
        break;
      case 'amount':
        comparison = a.amount - b.amount;
        break;
      case 'deposit':
        comparison = a.deposit - b.deposit;
        break;
      default:
        comparison = a.day - b.day;
    }
    
    return sortDirection === 'asc' ? comparison : -comparison;
  });
  
  return (
    <div className="bg-gray-800 rounded-lg shadow-lg overflow-hidden">
      <div className="p-4 border-b border-gray-700">
        <h2 className="text-xl font-semibold text-blue-300"></h2>
      </div>
      
      {days.length > 0 ? (
        <div className="overflow-x-auto">
          <table className="min-w-full table-responsive">
            <thead>
              <tr className="bg-gray-700">
                <th 
                  className="px-4 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider cursor-pointer"
                  onClick={() => handleSort('day')}
                >
                  <div className="flex items-center">
                    
                    {sortField === 'day' && (
                      <span className="ml-1">
                        {sortDirection === 'asc' ? '' : ''}
                      </span>
                    )}
                  </div>
                </th>
                <th 
                  className="px-4 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider cursor-pointer"
                  onClick={() => handleSort('date')}
                >
                  <div className="flex items-center">
                    
                    {sortField === 'date' && (
                      <span className="ml-1">
                        {sortDirection === 'asc' ? '' : ''}
                      </span>
                    )}
                  </div>
                </th>
                <th 
                  className="px-4 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider cursor-pointer"
                  onClick={() => handleSort('percentage')}
                >
                  <div className="flex items-center">
                    
                    {sortField === 'percentage' && (
                      <span className="ml-1">
                        {sortDirection === 'asc' ? '' : ''}
                      </span>
                    )}
                  </div>
                </th>
                <th 
                  className="px-4 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider cursor-pointer"
                  onClick={() => handleSort('amount')}
                >
                  <div className="flex items-center">
                    
                    {sortField === 'amount' && (
                      <span className="ml-1">
                        {sortDirection === 'asc' ? '' : ''}
                      </span>
                    )}
                  </div>
                </th>
                <th 
                  className="px-4 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider cursor-pointer"
                  onClick={() => handleSort('deposit')}
                >
                  <div className="flex items-center">
                    
                    {sortField === 'deposit' && (
                      <span className="ml-1">
                        {sortDirection === 'asc' ? '' : ''}
                      </span>
                    )}
                  </div>
                </th>
                <th className="px-4 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">
                  
                </th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-700">
              {sortedDays.map((day, index) => (
                <tr key={index} className="hover:bg-gray-700">
                  <td className="px-4 py-3 whitespace-nowrap">
                    <div className="text-sm text-white">{day.day}</div>
                  </td>
                  <td className="px-4 py-3 whitespace-nowrap">
                    <div className="text-sm text-white">{day.date}</div>
                  </td>
                  <td className="px-4 py-3 whitespace-nowrap">
                    <div className={`text-sm font-medium ${day.percentage >= 0 ? 'text-green-400' : 'text-red-400'}`}>
                      {day.percentage.toFixed(2)}%
                    </div>
                  </td>
                  <td className="px-4 py-3 whitespace-nowrap">
                    <div className={`text-sm font-medium ${day.amount >= 0 ? 'text-green-400' : 'text-red-400'}`}>
                      ${day.amount.toFixed(2)}
                    </div>
                  </td>
                  <td className="px-4 py-3 whitespace-nowrap">
                    <div className="text-sm text-white">${day.deposit.toFixed(2)}</div>
                  </td>
                  <td className="px-4 py-3 whitespace-nowrap text-right text-sm font-medium">
                    <div className="flex space-x-2">
                      <button
                        onClick={() => {
                          startEditingDay(index);
                          setActiveSection('dashboard');
                        }}
                        className="text-blue-400 hover:text-blue-300"
                      >
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                          <path d="M13.586 3.586a2 2 0 112.828 2.828l-.793.793-2.828-2.828.793-.793zM11.379 5.793L3 14.172V17h2.828l8.38-8.379-2.83-2.828z" />
                        </svg>
                      </button>
                      <button
                        onClick={() => archiveDay(index)}
                        className="text-yellow-400 hover:text-yellow-300"
                      >
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                          <path d="M4 3a2 2 0 100 4h12a2 2 0 100-4H4z" />
                          <path fillRule="evenodd" d="M3 8h14v7a2 2 0 01-2 2H5a2 2 0 01-2-2V8zm5 3a1 1 0 011-1h2a1 1 0 110 2H9a1 1 0 01-1-1z" clipRule="evenodd" />
                        </svg>
                      </button>
                      <button
                        onClick={() => deleteDay(index)}
                        className="text-red-400 hover:text-red-300"
                      >
                        <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                          <path fillRule="evenodd" d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
                        </svg>
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      ) : (
        <div className="p-6 text-center">
          <p className="text-gray-400">   </p>
          <button
            onClick={() => setActiveSection('dashboard')}
            className="mt-4 px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
          >
              
          </button>
        </div>
      )}
    </div>
  );
};

export default TransactionList; 