import React, { useState } from 'react';
import { exportDataToJson } from '../../utils/dataOperations';

/**
 * Settings component - handle application configuration and data management
 */
const Settings = ({
  leverage,
  setLeverage,
  dailyTarget,
  setDailyTarget,
  initialDeposit,
  setInitialDeposit,
  deposit,
  days,
  setDays,
  archivedDays,
  setArchivedDays,
  goals,
  setGoals,
  setActiveSection
}) => {
  const [newLeverage, setNewLeverage] = useState(leverage);
  const [newDailyTarget, setNewDailyTarget] = useState(dailyTarget);
  const [newInitialDeposit, setNewInitialDeposit] = useState(initialDeposit);
  const [showResetConfirm, setShowResetConfirm] = useState(false);
  const [resetType, setResetType] = useState('all');
  
  // Handle saving new settings
  const handleSaveSettings = () => {
    if (newLeverage > 0 && newDailyTarget > 0 && newInitialDeposit > 0) {
      setLeverage(newLeverage);
      setDailyTarget(newDailyTarget);
      setInitialDeposit(newInitialDeposit);
      alert('  !');
    } else {
      alert('     .');
    }
  };
  
  // Handle export data
  const handleExportData = () => {
    const dataToExport = {
      deposit,
      initialDeposit,
      leverage,
      dailyTarget,
      days,
      archivedDays,
      goals
    };
    
    exportDataToJson(dataToExport);
  };
  
  // Show reset confirmation
  const handleShowResetConfirm = (type) => {
    setResetType(type);
    setShowResetConfirm(true);
  };
  
  // Handle reset
  const handleReset = () => {
    switch (resetType) {
      case 'days':
        setDays([]);
        break;
      case 'archive':
        setArchivedDays([]);
        break;
      case 'goals':
        setGoals([]);
        break;
      case 'all':
        setDays([]);
        setArchivedDays([]);
        setGoals([]);
        setInitialDeposit(30);
        setLeverage(10);
        setDailyTarget(3);
        // Update the input fields
        setNewInitialDeposit(30);
        setNewLeverage(10);
        setNewDailyTarget(3);
        break;
      default:
        break;
    }
    
    setShowResetConfirm(false);
  };
  
  return (
    <div>
      <h1 className="text-3xl font-bold text-blue-300 mb-6"></h1>
      
      {/* General Settings */}
      <div className="bg-gray-800 p-5 rounded-lg mb-6">
        <h2 className="text-xl font-semibold text-blue-300 mb-4"> </h2>
        
        <div className="space-y-4">
          <div>
            <label className="block text-gray-300 mb-2">  ($):</label>
            <input
              type="number"
              min="0.01"
              step="0.01"
              value={newInitialDeposit}
              onChange={(e) => setNewInitialDeposit(parseFloat(e.target.value) || 0)}
              className="w-full p-3 bg-gray-700 border border-gray-600 rounded text-white"
            />
            <p className="text-gray-400 text-sm mt-1"> : ${initialDeposit}</p>
          </div>
          
          <div>
            <label className="block text-gray-300 mb-2"> (x):</label>
            <input
              type="number"
              min="1"
              step="1"
              value={newLeverage}
              onChange={(e) => setNewLeverage(parseFloat(e.target.value) || 0)}
              className="w-full p-3 bg-gray-700 border border-gray-600 rounded text-white"
            />
            <p className="text-gray-400 text-sm mt-1"> : {leverage}x</p>
          </div>
          
          <div>
            <label className="block text-gray-300 mb-2">    (%):</label>
            <input
              type="number"
              min="0.01"
              step="0.01"
              value={newDailyTarget}
              onChange={(e) => setNewDailyTarget(parseFloat(e.target.value) || 0)}
              className="w-full p-3 bg-gray-700 border border-gray-600 rounded text-white"
            />
            <p className="text-gray-400 text-sm mt-1"> : {dailyTarget}%</p>
          </div>
          
          <div className="flex justify-end pt-2">
            <button
              onClick={handleSaveSettings}
              className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
            >
               
            </button>
          </div>
        </div>
      </div>
      
      {/* Data Management */}
      <div className="bg-gray-800 p-5 rounded-lg mb-6">
        <h2 className="text-xl font-semibold text-blue-300 mb-4"> </h2>
        
        <div className="space-y-4">
          <div>
            <h3 className="text-lg text-gray-300 mb-2">/ </h3>
            <div className="flex flex-wrap gap-2">
              <button
                onClick={handleExportData}
                className="px-4 py-2 bg-green-600 text-white rounded hover:bg-green-700"
              >
                 
              </button>
              
              <button
                onClick={() => setActiveSection('dashboard')}
                className="px-4 py-2 bg-yellow-600 text-white rounded hover:bg-yellow-700"
              >
                 
              </button>
            </div>
            <p className="text-gray-400 text-sm mt-1">
                       .
            </p>
          </div>
          
          <div className="border-t border-gray-700 pt-4 mt-2">
            <h3 className="text-lg text-gray-300 mb-2"> </h3>
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-2">
              <button
                onClick={() => handleShowResetConfirm('days')}
                className="px-4 py-2 bg-red-600 text-white rounded hover:bg-red-700 opacity-80"
              >
                 
              </button>
              
              <button
                onClick={() => handleShowResetConfirm('archive')}
                className="px-4 py-2 bg-red-600 text-white rounded hover:bg-red-700 opacity-80"
              >
                 
              </button>
              
              <button
                onClick={() => handleShowResetConfirm('goals')}
                className="px-4 py-2 bg-red-600 text-white rounded hover:bg-red-700 opacity-80"
              >
                 
              </button>
              
              <button
                onClick={() => handleShowResetConfirm('all')}
                className="px-4 py-2 bg-red-600 text-white rounded hover:bg-red-700"
              >
                 
              </button>
            </div>
            <p className="text-gray-400 text-sm mt-1">
              :       .    .
            </p>
          </div>
        </div>
      </div>
      
      {/* App Info */}
      <div className="bg-gray-800 p-5 rounded-lg">
        <h2 className="text-xl font-semibold text-blue-300 mb-4"> </h2>
        
        <div className="space-y-2 text-gray-300">
          <p><span className="text-gray-400">:</span> 1.0.0</p>
          <p><span className="text-gray-400">:</span> </p>
          <p className="text-gray-400 text-sm mt-2">
                   .
          </p>
        </div>
      </div>
      
      {/* Reset Confirmation Modal */}
      {showResetConfirm && (
        <div className="fixed inset-0 bg-black bg-opacity-70 flex items-center justify-center z-50">
          <div className="bg-gray-800 p-6 rounded-lg max-w-md w-full">
            <h3 className="text-xl font-medium mb-4 text-red-400"> </h3>
            
            <p className="text-white mb-6">
              {resetType === 'days' && ' ,        ?'}
              {resetType === 'archive' && ' ,    ?'}
              {resetType === 'goals' && ' ,     ?'}
              {resetType === 'all' && ' ,       ?    !'}
            </p>
            
            <div className="flex justify-end space-x-3">
              <button
                onClick={() => setShowResetConfirm(false)}
                className="px-4 py-2 bg-gray-700 text-white rounded hover:bg-gray-600"
              >
                
              </button>
              
              <button
                onClick={handleReset}
                className="px-4 py-2 bg-red-600 text-white rounded hover:bg-red-700"
              >
                , 
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Settings; 